.search-box {
    /* margin-right: 12.5%; */
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
    height: 40px;
    background: #0b0b0d;
    border: 2px solid #d3d5fd;
    border-radius: 8px;
    color: white;
    text-align: center;
}
.go-back{cursor:pointer;height:45px;transition:transform .1s ease-in-out}.go-back:hover{transform:scale(1.1)}
.back-arrow{cursor:pointer;height:45px;transition:transform .1s ease-in-out}.back-arrow:hover{transform:scale(1.1)}
.forward-arrow{cursor:pointer;padding:0 5px;height:45px;rotate:180deg;transition:transform .1s ease-in-out}.forward-arrow:hover{transform:scale(1.1)}
.bar{display:flex;flex-direction:row;align-items:center;justify-content:space-between;width:100%;height:60px;background:#0c0c0c;z-index:2}.bar .go-back-container{width:260px;padding-left:40px}.bar .arrows-container{width:300px}.bar .arrows-container .arrows{text-align:right;margin-right:50px}
.assetImage {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
    width: 180px;
    height: 180px;
    margin: 20px;
    border-radius: 90px;
}

.header {
    display: grid;
    /* justify-content: center; */
    align-content: center;
    grid-template-columns: 1fr 10fr;
    background: #0b0b0d;
    border-radius: 20px;
    padding: 10px;
    box-shadow: 0px 1px 4px rgba(2, 2, 2, 0.1) inset, 0px 0px 15px rgba(12, 12, 12, 0.9);
}
.sub-info {
    background: #0b0b0d;
    box-shadow: 0px 1px 4px rgba(2, 2, 2, 0.1) inset, 0px 0px 15px rgba(12, 12, 12, 0.9);
    border-radius: 20px;
}
.details {
    display: grid;
    grid-template-rows: repeat(6, 1fr);
    grid-template-columns: 1fr 1fr;
    grid-column: span 2;
    align-items: center;
    background: #0b0b0d;
    padding: 10px;
    border-radius: 20px;
    box-shadow: 0px 1px 4px rgba(2, 2, 2, 0.1) inset, 0px 0px 15px rgba(12, 12, 12, 0.9);
    /* border-top: 1px solid #e4e5fc; */
}

.detailBox {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
body {
    color: #e4e5fc;
}

.assetBox {
    position: relative;
    display: grid;
    grid-template-rows: 1fr 3fr;
    grid-template-columns: minmax(450px, 1fr) minmax(600px, 2fr);
    grid-gap: 20px;
    justify-content: space-between;
    /* align-content: center; */
    margin: 20px 30px;
    height: 90%;
}

.headerInfo {
    grid-column: 2 / 3;
    margin-left: 50px;
}

.error-container{width:100%;height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;color:#fff;padding:20px 0}.error-container .error-page{color:#fff;margin:20px 0}.error-container .error{font-size:5em}.error-container .not-exist{font-size:2em}.error-container .link-page{color:#888}
.container {
    height: 100vh;
    z-index: 0;
}

.asset-container-box {
    height: 100%;
}

.asset-homepage {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 5em;
}

.App{display:grid;grid-template-columns:minmax(250px, 1fr) 5fr}
